<template>

    <el-container style="display: flex; flex-direction: row;">
        <sellHeader :inputData="inputData"></sellHeader>
        <masterInput :markdown="markdown" :strs="strs" :eHtml="eHtml"></masterInput>
        <mainVisibles :HTMLS="HTMLS"></mainVisibles>
    </el-container>

</template>

<script>
import sellHeader from './components/header.vue'
import masterInput from './components/input.vue'
import mainVisibles from './components/visible.vue'


export default {
    name: 'App',
    data() {
        return {
            strs: [],
            HTMLS: [],
            inputData: '',
        }
    },
    components: {
        sellHeader, masterInput, mainVisibles
    },
    methods: {
        markdown(str, inlnestr) {
            this.inputData = inlnestr;
            let index = 0;
            let start1 = []
            let start2 = []

            //标题的识别正则
            let prefixH1 = /^#(?!#)/
            let prefixH2 = /^(##)(?= )/
            let prefixH3 = /^(###)(?= )/
            let prefixH4 = /^(####)(?= )/
            let prefixH5 = /^(#####)(?= )/
            let prefixH6 = /^(######)(?= )/

            // 列表识别正则
            let nolist = /^[-](?= )/
            let list = /^[0-9]+\.(?= )/
            //引用>
            let reference = /^>(?= )/

            //双标签包裹类型的语法分析
            let stars = /^\*\*(.*?)\*\*/
            let star = /^\*(.*?)\*/
            let del = /^~~(.*?)~~/

            //链接和图片
            let linkurl = /^\[([^\]]+)\]\(([^)]+)\)\s{2}/
            let img = /^!\[([^\]]+)\]\(([^)]+)\)\s{2}/

            //表格
            let table = /^\| ([^|]+) \| ([^|]+) \|\s{2}/
            let tableheader = /^\| +-+ +\| +-+ \|\s{2}/

            let content = /^(\S.*?)(?=  )/
            //一个正则，可以识别到一段前面被一个空格包裹，后面被两个空格包裹的内容
            let suffix = /^\s{2}/


            while (index < str.length - 1) {
                let rest = str.substring(index);
                //行级标题
                if (prefixH1.test(rest)) {//捕获#
                    let times = rest.match(/^#(?!#)/)[0];
                    index += times.length + 1;//让指针前进
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h1' });//把标签名写进去和索引加上如果有子代也要放进去
                    start2.push('')//把这些东西压栈
                }
                //  #region
                else if (prefixH2.test(rest)) {
                    let times = rest.match(/^(##)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h2' });
                    start2.push('')
                }
                else if (prefixH3.test(rest)) {
                    let times = rest.match(/^(###)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h3' });
                    start2.push('')
                }
                else if (prefixH4.test(rest)) {
                    let times = rest.match(/^(####)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h4' });
                    start2.push('')
                }
                else if (prefixH5.test(rest)) {
                    let times = rest.match(/^(#####)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h5' });
                    start2.push('')
                }
                else if (prefixH6.test(rest)) {
                    let times = rest.match(/^(######)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'h6' });
                    start2.push('')
                }
                //列表部分
                //无序列表,匹配-和*这两个无序内容
                else if (nolist.test(rest)) {
                    let times = rest.match(/^([-])(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'ul' });
                    start2.push('')
                }
                //有序列表，匹配数字.加空格的形式的内容
                else if (list.test(rest)) {
                    let times = rest.match(/^([0-9]+\.)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'ol' });
                    start2.push('')
                }
                //引用部分
                else if (reference.test(rest)) {
                    let times = rest.match(/^(>)(?= )/)[0];
                    index += times.length + 1;
                    start1.push({ label: times, index: index, value: '', inline: false, HTML: 'blockquote' });
                    start2.push('')
                }
                // 水平线
                else if (/^(---|\*\*\*)\s\s/.test(rest)) {
                    let times = rest.match(/^(---|\*\*\*)\s\s/)[1];
                    index += times.length + 2;
                    start2.push({ label: times, index: index, value: '这是水平线', inline: false, HTML: 'hr' });
                }

                //  #endregion
                //双标签识别
                //**
                else if (stars.test(rest)) {
                    let keys = rest.match(stars)[1];
                    index += keys.length + 6;
                    start2.push({ label: '**', index: index, value: keys, inline: true, HTML: 'strong' });
                }
                //*
                else if (star.test(rest)) {
                    let keys = rest.match(star)[1];
                    index += keys.length + 4;
                    start2.push({ label: '*', index: index, value: keys, inline: true, HTML: 'em' });
                }
                else if (del.test(rest)) {
                    let keys = rest.match(del)[1];
                    index += keys.length + 6;
                    start2.push({ label: '~~', index: index, value: keys, inline: true, HTML: 'del' });
                }
                //链接
                else if (linkurl.test(rest)) {
                    let keys = rest.match(linkurl)[0];
                    let link = rest.match(linkurl)[1];
                    let url = rest.match(linkurl)[2];
                    index += keys.length;
                    start2.push({ label: '[]()', index: index, value: link, url: url, inline: true, HTML: 'a' });
                }
                else if (img.test(rest)) {
                    let keys = rest.match(img)[0];
                    let altText = rest.match(img)[1];
                    let imgurl = rest.match(img)[2];
                    index += keys.length;
                    start2.push({ label: '![]()', index: index, value: altText, url: imgurl, inline: true, HTML: 'img' });
                }
                //表格
                else if (table.test(rest)) {
                    if (tableheader.test(rest)) {
                        start2[start2.length - 1].header = true;
                        index += 25
                        continue;
                    }
                    let keys = rest.match(table)[0];
                    let table1 = rest.match(table)[1];
                    let table2 = rest.match(table)[2];
                    index += keys.length;
                    start2.push({ label: '| | |', index: index, value: table1 + '|' + table2, inline: true, HTML: 'table', next: '', header: false });
                }
                //内容拿去和末尾存放
                //这个地方有一个很严重的错误
                else if (content.test(rest)) {
                    let word = rest.match(/^(\S.*?)(?=  )/)[0];
                    start2[start2.length - 1] = word
                    index += word.length;
                }
                else if (suffix.test(rest)) {
                    let word = start1.pop();
                    let Str = start2.pop();
                    word.value = Str;
                    word.inline = true;
                    start2.push(word);
                    index += 2;
                }
                else {
                    throw new Error('find error' + '\n' + 'markdown语法错误,请检查输入内容');

                }
            }
            this.strs = start2;
            index = 0;
            console.log(start2);
            this.eHtml(this.strs);
            start2 = [];
        },

        eHtml(strs) {
            let HTML = []
            let UL = []
            let OL = []
            let content = []

            //普通情况
            function normal(label) {
                let html = label.HTML
                let value = label.value
                let mack = `<${html}>${value}</${html}>`
                HTML.push(mack)
            }
            //特别情况-无序列表
            // eslint-disable-next-line
            function ul(label) {
                let value = label.value
                UL.push(`<li>${value}</li>`)
            }
            function uls() {
                let mack = UL.join('\n')
                UL = []
                HTML.push(`<ul>${mack}</ul>`)
            }
            //特殊情况-有序列表
            // eslint-disable-next-line
            function ol(label) {
                let value = label.value
                OL.push(`<li>${value}</li>`)
            }
            function ols() {
                let mack = OL.join('\n')
                OL = []
                HTML.push(`<ol>${mack}</ol>`)
            }
            //特殊情况-水平线
            // eslint-disable-next-line
            function hr(label) {
                let mack = '<hr>'
                HTML.push(mack)
            }
            //特殊情况-链接
            // eslint-disable-next-line
            function a(label) {
                let value = label.value
                let url = label.url
                let mack = `<a href="${url}">${value}</a>`
                HTML.push(mack)
            }
            //特殊情况-图片
            // eslint-disable-next-line
            function img(label) {
                let altText = label.value
                let imgurl = label.url
                let mack = `<img src="${imgurl}"><br><p>${altText}</p>`
                HTML.push(mack)
            }
            //特殊情况-表格
            function tables(label,nextLabel) {
                
                if (label.header) {
                    let keys = label.value.split('|');
                    content.push(`<thead><tr><th>${keys[0]}</th><th>${keys[1]}</th></tr></thead>`) 
                }//可以改成支持更多列的类型
                else {
                    let keys = label.value.split('|');
                    content.push(`<tr><td>${keys[0]}</td><td>${keys[1]}</td></tr>`);
                }
                //拼接
                if (nextLabel?.HTML != 'table'|| nextLabel==undefined) {
                    let header = content[0]
                    content[0]=''
                    let start = content.join('\n')
                    let mack = `<table border='1'>${header}<tbody>${start}</tbody></table>`
                    HTML.push(mack)
                }
            }



            //开始遍历数组，使用if来分辨
            for (let i = 0; i < strs.length; i++) {
                if (['h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'blockquote', 'strong', 'em', 'del'].includes(strs[i]?.HTML)) {
                    normal(strs[i]);
                }
                else if (strs[i]?.HTML == 'ul') {
                    ul(strs[i]);
                    if (strs[i + 1]?.HTML != 'ul') {
                        uls()
                    }
                }
                else if (strs[i]?.HTML == 'ol') {
                    ol(strs[i]);
                    if (strs[i + 1]?.HTML != 'ol') {
                        ols()
                    }
                }
                else if (strs[i]?.HTML == 'hr') {
                    hr(strs[i]);
                }
                else if (strs[i]?.HTML == 'a') {
                    a(strs[i]);
                }
                else if (strs[i]?.HTML == 'img') {
                    img(strs[i]);
                }else if (strs[i]?.HTML == 'table') {
                    tables(strs[i],strs[i+1]);
                }

            }
            this.HTMLS = HTML


        },
        
    }
}



</script>

<style>
* {
    font-family: Hiragino Sans GB;
    
}

body {
    background-color: #ffffff;
    height: 100%;
    width: 100%;
    position: relative;
    background-attachment: fixed;
    font-size: 100%;
    overflow-x: hidden;
}




#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    margin-top: 5px;
}

.el-container {
    height: 1000px;
    flex-direction: column;
    width: 1500px;
}


@media print {
    #aside,
    #header,
    #visibleHeader {
        display: none;
    }
}
</style>
