<template>
  <el-aside id="aside">
    <el-input v-model="textarea2" :autosize="{ minRows: 47.2, maxRows: 2000 }" type="textarea" placeholder="输入内容"
      id="inputs" @keyup.enter="handleInput" style="flex: 1;" />
  </el-aside>
</template>



<script>

export default {
  name: 'masterInput',
  data() {
    return {
      textarea2: "",
      isCtrlPressed: false,
    }
  },
  methods: {
    handleInput(e) {
      let inputValue = e.target.value;
      let inputValues = inputValue.replace(/[\n\r]+/g, '');
      this.markdown(inputValues, inputValue);

      // 调用父组件传入的函数并传递输入的值
    },
    handleInput2(inputValue) {
      let inputValues = inputValue.replace(/[\n\r]+/g, '');
      this.markdown(inputValues, inputValue);
    },
    //不是用于点击的方法，用于header.vue来调用的方法
    handleKeyDown(event) {
      if (event.key === 'Control') {
        this.isCtrlPressed = true;
      }

      //监听快捷键
      if (this.isCtrlPressed && event.key === '1') {
        event.preventDefault(); // 防止浏览器默认行为
        this.textarea2 += '# ';
      } else if (this.isCtrlPressed && event.key === '2') {
        event.preventDefault();
        this.textarea2 += '## ';
      } else if (this.isCtrlPressed && event.key === '3') {
        event.preventDefault();
        this.textarea2 += '### ';
      } else if (this.isCtrlPressed && event.key === '4') {
        event.preventDefault();
        this.textarea2 += '#### ';
      } else if (this.isCtrlPressed && event.key === '5') {
        event.preventDefault();
        this.textarea2 += '##### ';
      } else if (this.isCtrlPressed && event.key === '6') {
        event.preventDefault();
        this.textarea2 += '###### ';
      }else if (this.isCtrlPressed && event.key === 'B') {
        event.preventDefault();
        this.textarea2 += '** 内容 **  ';
      }else if (this.isCtrlPressed && event.key === 'Q') {
        event.preventDefault();
        this.textarea2 += '> 内容  ';
      }else if (this.isCtrlPressed && event.key === 'I') {
        event.preventDefault();
        this.textarea2 += '* 内容 *  ';
      }else if (this.isCtrlPressed && event.key === 'U') {
        event.preventDefault();
        this.textarea2 += '- 内容  ';
      }else if (this.isCtrlPressed && event.key === 'L') {
        event.preventDefault();
        this.textarea2 += '[链接](链接地址)  ';
      }
    }
  },

  props: {
    markdown: Function,
    eHtml: Function,
    strs: Array
  },

  mounted() {
    this.eventBusListener = this.$bus.on('eventBus', (val) => {
      if (typeof val === 'string') {
        this.textarea2 = val;
        this.handleInput2(this.textarea2);
      } else {
        console.warn('Received unexpected value type:', val);
      }
    });

    this.eventBusListeners = this.$bus.on('addArrowRight', (val) => {
      if (typeof val === 'string') {
        this.textarea2 += val;
      } else {
        console.warn('Received unexpected value type:', val);
      }
    });
    this.eventBusListeners1 = this.$bus.on('addLink', (val) => {
      if (typeof val === 'string') {
        this.textarea2 += val;
      } else {
        console.warn('Received unexpected value type:', val);
      }
    });
    this.eventBusListeners2 = this.$bus.on('DeleteData', (val) => {

      if (typeof val === 'string') {
        this.textarea2 = val;
        this.handleInput2(this.textarea2);
      } else {
        console.warn('Received unexpected value type:', val);
      }
    });
    this.eventBusListeners3 = this.$bus.on('addList', (val) => {
      this.textarea2 += val;
    });
    this.eventBusListeners4 = this.$bus.on('add', (val) => {
      this.textarea2 += val;
    });
    this.eventBusListeners5 = this.$bus.on('addRemove', (val) => {
      this.textarea2 += val;
    });
    this.eventBusListeners6 = this.$bus.on('tabels', (val) => {
      this.textarea2 += val;
    });

  },
  beforeDestroy() {
    if (this.eventBusListener) {
      this.$bus.off('eventBus', this.eventBusListener);
    }
    if (this.eventBusListeners) {
      this.$bus.off('addArrowRight', this.eventBusListeners);
    }
    if (this.eventBusListeners1) {
      this.$bus.off('addLink', this.eventBusListeners1);
    }
    if (this.eventBusListeners2) {
      this.$bus.off('DeleteData', this.eventBusListeners2);
    }
    if (this.eventBusListeners3) {
      this.$bus.off('addList', this.eventBusListeners3);
    }
    if (this.eventBusListeners4) {
      this.$bus.off('add', this.eventBusListeners4);
    }
    if (this.eventBusListeners5) {
      this.$bus.off('addRemove', this.eventBusListeners5);
    }
    if (this.eventBusListeners6) {
      this.$bus.off('tables', this.eventBusListeners6);
    }

    window.removeEventListener('keydown', this.handleKeyDown);
  },
  created() {
    // 在组件创建时添加全局按键事件监听器
    window.addEventListener('keydown', this.handleKeyDown);
  },

}

</script>

<style>
:root {
  --inputs: 640px;
}


#inputs {
  position: relative;
  flex: 1;
  top: 30px;
  resize: unset;
  background-color: transparent;
}

#aside::-webkit-resizer {
  width: 100px;
  height: 1000px;
  background-color: rgb(164, 164, 164);
  border-radius: 5px;
  transition: all .1s ease;

}

#aside {
  position: relative;
  resize: horizontal;
  overflow: hidden;
  height: 103.5%;
  width: var(--inputs);
  display: flex;
}
</style>