<template>
  <el-header class="header" id="header">
    <el-icon>
      <House />
    </el-icon>
    <el-icon>
      <Document @click="tabels" />
    </el-icon>
    <el-icon>
      <SemiSelect @click="addRemove" />
    </el-icon>
    <el-icon>
      <ChatLineRound @click="add" />
    </el-icon>
    <el-icon>
      <CirclePlus @click="drawer" />
    </el-icon>
    <el-icon>
      <List @click="addList" />
    </el-icon>
    <el-icon>
      <ArrowRight @click="addArrowRight" />
    </el-icon>
    <el-icon style="border-right: 2px solid #999999; padding-right: 10px;">
      <Link @click="addLink" />
    </el-icon>
    <el-icon>
      <Tools />
    </el-icon>
    <el-icon class="no-inherit">
      <Share @click="share" />
    </el-icon>


    <el-popconfirm title="是否确认清空内容？" confirm-button-text="Yes" cancel-button-text="No" :icon="InfoFilled"
      @confirm="DeleteData">
      <template #reference>
        <el-icon style="border-right: 2px solid #999999; padding-right: 10px;">
          <Delete />
        </el-icon>
      </template>
    </el-popconfirm>


    <el-icon class="is-loading">
      <Loading />
    </el-icon>
    <el-icon @click="backgroundColor">
      <Open v-if="h" />
      <TurnOff v-if="!h" />
    </el-icon>
  </el-header>


  <!-- 相关抽屉的代码 -->
  <el-drawer v-model="drawers" title="file" :with-header="false" direction="btt">

    <div style="position: sticky; z-index: 10001; top: 0; background-color: white;">
      <hr>
      <el-button type="primary" :icon="Search">搜索</el-button>
      <el-button type="primary" @click="upData">
        上传<el-icon class="el-icon--right">
          <Upload />
        </el-icon>
      </el-button>
      <hr>
    </div>

    <el-table ref="singleTableRef" :data="tableData" highlight-current-row style="width: 100%"
      @current-change="handleCurrentChange">
      <el-table-column type="index" width="50" />
      <el-table-column prop="date" label="Date" width="180" />
      <el-table-column prop="name" label="Name" width="180" />
      <el-table-column prop="address" label="Address" />
    </el-table>
  </el-drawer>
</template>


<script>
import { ElMessage, ElMessageBox } from 'element-plus'





export default {
  name: 'sellHeader',
  data() {
    return {
      drawers: false,
      tableData: [],
      ii: 1,
      h: true,
    }
  },
  methods: {
    handleClick(command) {
      this.$message.info(`点击了 ${command}`)
    },
    drawer() {
      this.drawers = true
    },
    handleCurrentChange(val) {
      this.$bus.emit('eventBus', val.connet)
    },
    // 两种本地缓存思路，将其做成很多个文件的形式，来储存，或者提供随时的缓存，使其更加方便，但是需要解决冲突的问题
    upData() {
      let datas = []
      let name = this.inputData.substring(0, this.inputData.indexOf('\n')) + '...'
      let address = this.inputData.substring(0, this.inputData.indexOf('\n'))
      let value = { date: `${new Date().toLocaleString()}`, name: name, address: address, connet: `${this.inputData}` }
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].connet === this.inputData) {
          return
        }
        else if (this.tableData[i].name === name) {
          this.tableData[i] = value
          localStorage.setItem('inputData', JSON.stringify(this.tableData))
          return
        }
      }
      //date是一个储存日期，name是标题，address是浏览器的本地储存的值
      if (localStorage.getItem('inputData') !== null) {
        datas = JSON.parse(localStorage.getItem('inputData'))
      }
      datas.push(value)
      localStorage.setItem('inputData', JSON.stringify(datas))
      this.tableData = JSON.parse(localStorage.getItem('inputData'))
      //这个函数是处理储存的数据，如果存在就覆盖，不存在就添加
    },
    addArrowRight() {
      this.$bus.emit('addArrowRight', '> ')
    },
    addLink() {
      this.$bus.emit('addLink', '[connet](url)  ')
    },
    DeleteData() {
      this.$bus.emit('DeleteData', '')
    },
    addList() {
      this.$bus.emit('addList', '- ')
    },
    add() {
      this.$bus.emit('add', `${this.ii++}. `)
    },
    addRemove() {
      this.$bus.emit('addRemove', '~~ 内容 ~~  ')
    },
    share() {
      let currentURL = window.location.href;
      ElMessageBox.alert(
        `<img src="https://api.pwmqr.com/qrcode/create/?url=${currentURL}&down=1">`,
        {
          dangerouslyUseHTMLString: true,
        }
      )
    },
    tabels() {
      this.$bus.emit('tabels', '| 标题 | 标题 |  \n| -------- | -------- |  \n| 内容 | 内容 |  ')
    },
    // 全局主题的修改
    backgroundColor() {
      this.h = !this.h
      if (!this.h) {
        document.body.style.backgroundImage = 'url(https://api.paugram.com/wallpaper/) '
        document.body.style.backgroundRepeat = 'no-repeat'
        document.body.style.backgroundSize = 'cover'
        document.body.style.backgroundPosition = 'center'
      } else {
        document.body.style.background = '#ffffff'
      }
    },

  },
  props: {
    inputData: String
  },

  beforeMount() {
    let data1 = JSON.parse(localStorage.getItem('inputData'))
    if (data1) {
      this.tableData = data1
    }
  },
  //这两个钩子是为了解决本地缓存的问题
  mounted() {
    let data2 = JSON.parse(localStorage.getItem('inputData'))
    if (data2) {
      this.tableData = data2
    }
  },



}

</script>



<style>
#header {
  --el-header-height: 25px;
  border-radius: 20px;
  border: 2px solid #505050;
  width: 100%;
  position: fixed;
  left: 0;
  z-index: 1000;
}

.header i {
  margin: 3px 20px auto 20px;
  float: left;
  transition: color 0.1s ease;
}


.header i:active {
  color: #409eff;
}
</style>
