// 创建事件总线
const emitter = mitt()


//element-plus的创建
const app = createApp(App)
//全局事件实例

// 将事件总线挂载到全局属性
app.config.globalProperties.$bus = emitter

app.config.productionTip = false

for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

app.use(ElementPlus)

// 将事件总线挂载到全局属性上



app.mount('#app')
